import React, { useEffect, useState } from "react";
import { clamp } from "./Utils";
import styles from "./Pager.module.css";

type GetPagesArgs = {
  currentPage: number;
  pageDisplayLimit: number;
  totalPages: number;
};

function getPages({ currentPage, totalPages, pageDisplayLimit }: GetPagesArgs) {
  const minPageNumber = 1;
  const middlePageNumberStart = currentPage - Math.floor(pageDisplayLimit / 2);
  const maxPageNumberStart = Math.max(0, totalPages - pageDisplayLimit) + 1;

  let displayedPagesStart = clamp(
    minPageNumber,
    middlePageNumberStart,
    maxPageNumberStart
  );

  const displayedPagesCount = Math.min(pageDisplayLimit, totalPages);

  let pageNumbers = Array(displayedPagesCount)
    .fill(undefined)
    .map((_, i) => i + displayedPagesStart);

  return pageNumbers;
}

type PagerProps = {
  pageDisplayLimit: number;
  pageSize: number;
  itemCount: number;
  currentPage: number;
  setCurrentPage: React.Dispatch<React.SetStateAction<number>>;
};

export function Pager({
  pageDisplayLimit,
  pageSize,
  itemCount,
  currentPage,
  setCurrentPage,
}: PagerProps) {
  const [pageNumbers, setPageNumbers] = useState<number[]>([]);

  const totalPages = Math.ceil(itemCount / pageSize);
  const isOnLastPage = currentPage === totalPages;
  const isOnFirstPage = currentPage === 1;

  useEffect(() => {
    const pages =
      totalPages === 1
        ? []
        : getPages({ currentPage, pageDisplayLimit, totalPages });

    setPageNumbers(pages);
  }, [pageDisplayLimit, itemCount, currentPage, totalPages]);

  function goToFirstPage() {
    setCurrentPage(1);
  }

  function goToPreviousPage() {
    setCurrentPage((currentPage) => currentPage - 1);
  }

  function goToPage(page: number) {
    if (page === currentPage) {
      return;
    }

    setCurrentPage(page);
  }

  function goToNextPage() {
    setCurrentPage((currentPage) => currentPage + 1);
  }

  function goToLastPage() {
    setCurrentPage(totalPages);
  }

  return pageNumbers.length > 0 ? (
    <span>
      <button
        disabled={isOnFirstPage}
        onClick={goToFirstPage}
        className={styles["pager-button"]}
      >
        « First
      </button>
      <button
        disabled={isOnFirstPage}
        onClick={goToPreviousPage}
        className={styles["pager-button"]}
      >
        ‹ Prev
      </button>
      {pageNumbers.map((p) => (
        <button
          key={p}
          onClick={() => goToPage(p)}
          className={`${styles["pager-button"]} ${
            p === currentPage ? styles.selected : ""
          }`}
        >
          {p}
        </button>
      ))}
      <button
        disabled={isOnLastPage}
        onClick={goToNextPage}
        className={styles["pager-button"]}
      >
        Next ›
      </button>
      <button
        disabled={isOnLastPage}
        onClick={goToLastPage}
        className={styles["pager-button"]}
      >
        Last »
      </button>
    </span>
  ) : (
    <></>
  );
}
