import React, { useEffect, useState } from "react";
import { BookGallery } from "./Books";
import { SearchFilters } from "./Filter";
import "./Search.css";
import { Book, Medium, SearchCriteria } from "./types";
import styles from "./Search.module.css";
import { Pager } from "./Pager";

type SearchToolbarProps = {
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

export function SearchToolbar({
  searchCriteria,
  setSearchCriteria,
}: SearchToolbarProps) {
  function setSearchText(searchText: string) {
    setSearchCriteria((c) => {
      return { ...c, searchText };
    });
  }

  return (
    <div className={styles["search-control"]}>
      <i className={`fas fa-search`}></i>
      <input
        type="text"
        name="Search"
        id="search"
        className={styles.search}
        placeholder="Search..."
        value={searchCriteria.searchText}
        onChange={(e) => setSearchText(e.target.value)}
      />
    </div>
  );
}

type SearchProps = {
  books: Book[];
  tags: string[];
  genres: string[];
  authors: string[];
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

export function Search({
  books,
  searchCriteria,
  setSearchCriteria,
  tags,
  genres,
  authors,
}: SearchProps) {
  const [searchResults, setSearchResults] = useState<Book[]>([]);
  const [searchResultPage, setSearchResultPage] = useState<Book[]>([]);
  const [filterMenuOpen, setFilterMenuOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  const pageDisplayLimit = 5;
  const pageSize = 20;

  useEffect(() => {
    const results = books.filter(
      (b) =>
        filterBySearchText(searchCriteria.searchText, b) &&
        filterByGenre(searchCriteria.genres, b) &&
        filterByTags(searchCriteria.tags, b) &&
        filterByMedia(searchCriteria.media, b)
    );

    setSearchResults(results);
  }, [books, currentPage, searchCriteria]);

  useEffect(() => {
    const start = (currentPage - 1) * pageSize;
    const pagedResults = searchResults.slice(start, start + pageSize);
    setSearchResultPage(pagedResults);
  }, [currentPage, searchResults]);

  function toggleFilterMenu() {
    setFilterMenuOpen((option) => !option);
  }

  return (
    <div className={styles["search-container"]}>
      <div className={styles["search-bar-container"]}>
        <SearchToolbar
          searchCriteria={searchCriteria}
          setSearchCriteria={setSearchCriteria}
        />
      </div>
      <div
        className={`${styles["search-filter-container"]} ${
          filterMenuOpen ? styles.open : ""
        }`}
      >
        <SearchFilters
          tags={tags}
          genres={genres}
          authors={authors}
          searchCriteria={searchCriteria}
          setSearchCriteria={setSearchCriteria}
        />
      </div>
      <button
        onClick={toggleFilterMenu}
        className={styles["filter-button"]}
        aria-label="Filter Menu Toggle"
      >
        <i className={filterMenuOpen ? "fas fa-times" : "fas fa-filter"}></i>
      </button>
      <div className={styles["search-results-container"]}>
        <div className={styles["pager-container"]}>
          <Pager
            currentPage={currentPage}
            itemCount={searchResults.length}
            pageDisplayLimit={pageDisplayLimit}
            pageSize={pageSize}
            setCurrentPage={setCurrentPage}
          />
        </div>

        <BookGallery books={searchResultPage} />
        <div className={styles["pager-container"]}>
          <Pager
            currentPage={currentPage}
            itemCount={searchResults.length}
            pageDisplayLimit={pageDisplayLimit}
            pageSize={pageSize}
            setCurrentPage={setCurrentPage}
          />
        </div>
      </div>
    </div>
  );
}
function filterBySearchText(searchText: string | null, b: Book): unknown {
  return (
    searchText === null ||
    b.Title.toLowerCase().includes(searchText.toLowerCase()) ||
    b.AuthorFullName.toLowerCase().includes(searchText.toLowerCase())
  );
}
function filterByGenre(genres: string[], book: Book) {
  return (
    genres.length === 0 ||
    genres.reduce<boolean>(
      (acc, genre) =>
        acc && book.Genre?.toLowerCase()?.trim() === genre.toLowerCase().trim(),
      true
    )
  );
}

function filterByTags(tags: string[], book: Book) {
  return (
    tags.length === 0 ||
    tags.reduce<boolean>(
      (acc, tag) =>
        acc &&
        book.Tags.some(
          (t) => t?.toLowerCase().trim() === tag?.toLowerCase().trim()
        ),
      true
    )
  );
}

function filterByMedia(media: Medium[], b: Book): unknown {
  return (
    media.length === 0 ||
    media.reduce<boolean>((acc, medium) => {
      switch (medium) {
        case "Audiobook":
          return acc && b.AudioBook;
        case "eBook":
          return acc && b.Ebook;
        case "Physical":
          return acc && b.Physical;
        default:
          throw new Error(
            `Medium not found. Unable to search by medium ${medium}`
          );
      }
    }, true)
  );
}
